/* Styles for the home page */

.home {
  display: flex;
  flex-direction: column;
  background: var(--color-bg);
  color: var(--color-text);
}

/* Hero section */
.hero {
  padding: 4rem 1rem;
  background: linear-gradient(135deg, #1e3a8a, #9333ea);
  color: #fff;
  text-align: center;
}

.hero__inner {
  max-width: 900px;
  margin: 0 auto;
}

.hero__title {
  font-size: clamp(2rem, 5vw + 1rem, 3.5rem);
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.hero__subtitle {
  font-size: clamp(1rem, 1.5vw + 0.5rem, 1.25rem);
  margin-bottom: 2rem;
  color: #e0e7ff;
}

.hero__cta {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background: var(--color-primary);
  color: #fff;
  border-radius: 0.375rem;
  font-weight: 600;
  text-decoration: none;
  transition: background 0.3s ease, transform 0.2s ease;
}

.hero__cta:hover {
  background: var(--color-secondary);
  transform: translateY(-2px);
}

/* Features section */
.features {
  padding: 4rem 1rem;
  background: var(--color-surface);
}

.features__heading {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: var(--color-primary);
  font-weight: 700;
}

.features__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.feature {
  background: #131c35;
  border-radius: 0.5rem;
  padding: 2rem 1.5rem;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.3);
}

.feature__title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  color: var(--color-secondary);
}

.feature__desc {
  font-size: 0.95rem;
  line-height: 1.4;
  margin-bottom: 0.75rem;
  color: var(--color-muted);
}

.feature__list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 0.9rem;
  color: var(--color-text);
}

.feature__list li {
  margin-bottom: 0.4rem;
  position: relative;
  padding-left: 1rem;
}

.feature__list li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0.45rem;
  width: 6px;
  height: 6px;
  background: var(--color-primary);
  border-radius: 50%;
}

/* Flutch.ai section */
.flutch {
  padding: 4rem 1rem;
  background: linear-gradient(135deg, #4c1d95, #2563eb);
  color: #fff;
}

.flutch__inner {
  max-width: 1200px;
  margin: 0 auto;
}

.flutch__title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
}

.flutch__subtitle {
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: #fbbf24;
  font-weight: 600;
}

.flutch__content {
  text-align: center;
  font-size: 1.1rem;
  margin-bottom: 3rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  color: #e0e7ff;
}

.flutch__features {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .flutch__features {
    grid-template-columns: 1fr;
  }
}

.flutch__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.flutch__list li {
  margin-bottom: 1rem;
  padding-left: 1.5rem;
  position: relative;
}

.flutch__list li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: #10b981;
  font-weight: bold;
}

.flutch__package {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 0.5rem;
  padding: 1.5rem;
  backdrop-filter: blur(10px);
}

.flutch__package h4 {
  margin-bottom: 1rem;
  font-size: 1.2rem;
  color: #fbbf24;
}

.flutch__package ul {
  list-style: none;
  padding: 0;
  margin: 0 0 1rem 0;
}

.flutch__package ul li {
  margin-bottom: 0.5rem;
  padding-left: 1.5rem;
  position: relative;
}

.flutch__package ul li::before {
  content: '•';
  position: absolute;
  left: 0;
  color: #60a5fa;
}

.flutch__price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #10b981;
  margin-top: 1rem;
}

.flutch__actions {
  text-align: center;
  margin-top: 2rem;
}

.flutch__cta {
  display: inline-block;
  padding: 1rem 2rem;
  background: #fbbf24;
  color: #1e293b;
  border-radius: 0.5rem;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  font-size: 1.1rem;
}

.flutch__cta:hover {
  background: #f59e0b;
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}